import { useRecoilState } from 'recoil';
import { freeStatus, payStatus } from '../../store';
import {
  Category,
  ContentButton,
  Contents,
  FlexContainer,
  Subject,
} from './style';

const Categories = () => {
  const [freeCondition, setFreeCondition] = useRecoilState(freeStatus);
  const [payCondition, setPayCondition] = useRecoilState(payStatus);

  return (
    <>
      <FlexContainer>
        <Category>
          <Subject></Subject>
          <Contents>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
          </Contents>
        </Category>
        <Category>
          <Subject></Subject>
          <Contents>
            <ContentButton> </ContentButton>
            <ContentButton> </ContentButton>
          </Contents>
        </Category>
        <Category>
          <Subject></Subject>
          <Contents>
            <ContentButton> </ContentButton>
            <ContentButton> </ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
          </Contents>
        </Category>
        <Category>
          <Subject></Subject>
          <Contents>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
          </Contents>
        </Category>
        <Category>
          <Subject></Subject>
          <Contents>
            <ContentButton>C</ContentButton>
            <ContentButton>C++</ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
            <ContentButton>R</ContentButton>
            <ContentButton>HTML/CSS</ContentButton>
            <ContentButton>SQL</ContentButton>
            <ContentButton></ContentButton>
            <ContentButton></ContentButton>
          </Contents>
        </Category>
        <Category>
          <Subject></Subject>
          <Contents>
            <ContentButton
              onClick={() => setFreeCondition(prev => !prev)}
              className={freeCondition && 'active'}
            >
              
            </ContentButton>
            <ContentButton
              onClick={() => setPayCondition(prev => !prev)}
              className={payCondition && 'active'}
            >
              
            </ContentButton>
          </Contents>
        </Category>
      </FlexContainer>
    </>
  );
};

export default Categories;
